<template>
    <div>
        <div class="title">
            <h3 class="yun-h3"> Toast</h3>
            <p>YunUI  <strong></strong>
                 <strong></strong> </p>
        </div>
        <div class="content button-container">
            <y-button type="linear" @click="showToast"></y-button>
            <y-button type="linear" @click="showToastBottom"></y-button>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'y-toast-demo',
        props: {},
        data: function () {
            return {}
        },
        created () {
            console.log('button demo loaded')
        },
        methods: {
            showToast: function () {
                this.$toast({
                    content: '',
                    type: 'success' || 'failed'
                })
            },
            showToastBottom: function () {
                this.$toast({
                    content: '',
                    bottom: true
                })
            }
        }
    }

</script>

<style lang="less">

</style>



