<template>
    <div>
        <div class="title">
            <h3 class="yun-h3"> Loading</h3>
            <p>YunUI 
                 <strong></strong> 
                 <strong></strong> </p>
        </div>
        <div class="content button-container">
            <y-button type="linear" @click.native="showFresh = !showFresh"> Loading</y-button>
            <y-button type="linear" @click.native="showLoading = !showLoading"> Loading</y-button>
        </div>

        <y-loading content="" refresh :show="showFresh" style="top:64px">
            <img src="../assets/loading.png" slot="icon"/>
        </y-loading>
        <y-loading content="" :show="showLoading">
            <img src="../assets/loading.png" slot="icon"/>
        </y-loading>
    </div>
</template>

<script>
    export default {
        name: 'y-loading-demo',
        props: {},
        data: function () {
            return {
                showFresh: false,
                showLoading: false
            }
        }
    }

</script>

<style lang="less">
    .is-loading-refresh {
        top: 64px;
    }
</style>



