<template>
    <div>
        <div class="title">
            <h3 class="yun-h3"> popup</h3>
            <p>YunUI </p>
        </div>
        <div class="content button-container">
            <y-button type="linear" @click="showPopup1 = true">
            </y-button>
            <y-button type="linear" @click.native="showPopup2 = !showPopup2"></y-button>
            <y-button type="linear" @click.native="showPopup3 = !showPopup3"></y-button>
            <y-button type="linear" @click.native="showPopup4 = !showPopup4"></y-button>
            <y-button type="linear" @click.native="showPopup5 = !showPopup5"></y-button>
        </div>

        <y-popup title="" :show="showPopup1" @maskClicked="maskClicked"
                   content="
                            ">
            <y-button-group slot="buttonGroup" defaultActiveTabId="1">
                <y-tab-item title="" tabId="1"></y-tab-item>
            </y-button-group>
        </y-popup>

        <y-popup title="" :show="showPopup2" @maskClicked="maskClicked"
                   input="input">
            <y-button-group slot="buttonGroup" defaultActiveTabId="1">
                <y-tab-item title="" tabId="1"></y-tab-item>
                <y-tab-item title="" tabId="2"></y-tab-item>
            </y-button-group>
        </y-popup>

        <y-popup title="" :show="showPopup3" @maskClicked="maskClicked"
                   input="textarea">
            <y-button-group slot="buttonGroup" defaultActiveTabId="1">
                <y-tab-item class="active" title="" tabId="1"></y-tab-item>
                <y-tab-item title="" tabId="2"></y-tab-item>
            </y-button-group>
        </y-popup>

        <y-popup title="" :show="showPopup4" @maskClicked="maskClicked"
                   content="
                            ">
            <y-button size="large" type="normal" slot="button"></y-button>
            <y-button size="large" type="normal" disabled slot="button"></y-button>
            <y-button-group slot="buttonGroup" defaultActiveTabId="1">
                <y-tab-item title="" tabId="1"></y-tab-item>
            </y-button-group>
        </y-popup>

        <y-popup title="" :show="showPopup5" @maskClicked="maskClicked"
                   :textList="textListArray">
            <img src="../assets/popup-cover.png" slot="cover"/>
            <y-button-group slot="buttonGroup" defaultActiveTabId="2">
                <y-tab-item title="" tabId="1"></y-tab-item>
                <y-tab-item title="" class="active" tabId="2"></y-tab-item>
            </y-button-group>
        </y-popup>
    </div>
</template>

<script>
    export default {
        name: 'y-toast-demo',
        props: {},
        data: function () {
            return {
                showPopup1: false,
                showPopup2: false,
                showPopup3: false,
                showPopup4: false,
                showPopup5: false,
                textListArray: [
                    {
                        content: ''
                    },
                    {
                        content: ''
                    },
                    {
                        content: ''
                    }
                ]
            }
        },
        created () {
            console.log('button demo loaded')
        },
        methods: {
            maskClicked: function () {
                this.showPopup1 = false
                this.showPopup2 = false
                this.showPopup3 = false
                this.showPopup4 = false
                this.showPopup5 = false
            }
        }
    }

</script>

<style lang="less" rel="stylesheet/less">
    .close-popup {
        position: fixed;
        z-index: 10;
        color: #ffffff;
        right: 12px;
        top: 12px;
        width: 44px;
        height: 44px;
        border-radius: 50%;
        line-height: 44px;
        text-align: center;
        background-color: rgba(255, 255, 255, 0.2);
        &:hover {
            background-color: rgba(255, 255, 255, 0.4);
            cursor: pointer;
        }
    }
</style>



